/* eslint-disable react/jsx-no-target-blank */
/* eslint-disable no-undef */
/**
 * Data Table
 */
import React from 'react'
import PlacesAutocomplete, {
    geocodeByAddress,
    getLatLng,
} from 'react-places-autocomplete'
import Timelist from './Timelist'
import Weeklydate from './Weeklydate'
import Personlist from './Personlist'
import { history } from '../../history'
import RctSectionLoader from 'Components/RctSectionLoader/RctSectionLoader'
import moment from 'moment'

const calendarId = process.env.REACT_APP_CALENDAR_ID
const scopes = 'https://www.googleapis.com/auth/calendar https://www.googleapis.com/auth/calendar.events'
const DISCOVERY_DOCS = ['https://www.googleapis.com/discovery/v1/apis/calendar/v3/rest']

class DataPage extends React.Component {
    constructor() {
        super()
        this.state = {
            address: '',
            selectedAddress: null,
            latLng: {},
            step: 0,
        }
    }

    componentDidMount() {
        this.props.loadGlobalSetting()
        this.props.loadBookings()
        const that = this
        function start() {
            gapi.client
                .init({
                    apiKey: process.env.REACT_APP_GOOGLE_API_KEY,
                    client_id: process.env.REACT_APP_CLIENT_ID,
                    discoveryDocs: DISCOVERY_DOCS,
                    scope: scopes,
                })
                .then(function () {
                    // Listen for sign-in state changes.
                    gapi.auth2.getAuthInstance().isSignedIn.listen(that.updateSigninStatus)

                    // Handle the initial sign-in state.
                    that.updateSigninStatus(gapi.auth2.getAuthInstance().isSignedIn.get())

                })
        }
        gapi.load('client:auth2', start)
    }

    /////////////////////
    getEvents = () => {
        
        gapi.client.request({
            path: `https://www.googleapis.com/calendar/v3/calendars/${calendarId}/events?timeMin=${moment().add(-15, 'days').toISOString()}&timeMax=${moment().add(15, 'days')
                .toISOString()}`,
        }).then(
            response => {
                const events = response.result.items
                
                for (let i = 0; i < this.props.bookingsData.length; i++) {
                    const bookingItem = this.props.bookingsData[i]
                    const findEvent  = events.find(eventItem => eventItem.id === bookingItem.eid)

                    if (!findEvent) {
                        this.addCalendar(bookingItem)
                    }

                }

                setTimeout(() => {
                    this.props.loadBookings()
                }, 5000)
            
            },
        )
        
    }

    addCalendar = (booking) => {
        // console.log(booking.pick_time)
        const endDate = new Date(booking.pick_time)
        // console.log(endDate)
        endDate.setHours(endDate.getHours() + 1)
        
        const event = {
            'summary': booking.address_name,
            'location': booking.address_name,
            'description': 'Site Visit Booking description',
            'start': {
                'dateTime': booking.pick_time,
                'timeZone': 'Australia/Sydney',
            },
            'end': {
                'dateTime': endDate,
                'timeZone': 'Australia/Sydney',
            },
            'recurrence': [
                
            ],
            'attendees': [
                booking.s_email,
            ],
            'reminders': {
            },
        }
          
        const request = gapi.client.calendar.events.insert({
            'calendarId': process.env.REACT_APP_CALENDAR_ID,
            'resource': event,
        })

        const that = this
        request.execute(function(event1) {
            // console.log('Event created:')
            // console.log(event1)

            if (event1) {
                booking.eid = event1.id
                that.props.updateBook(booking)
            }
        })
    }

    updateSigninStatus = (isSignedIn) => {
        // console.log('login' + isSignedIn)
        if (isSignedIn) {
            this.setState({sign: true})
            setTimeout(() => {
                this.getEvents()
            }, 5000)
            
        } else {
            // console.log('login' + isSignedIn)
            gapi.auth2.getAuthInstance().signIn()
        }
    }

    handleChange = address => {
        this.setState({ address, step: 0 })
        this.props.clearTopAddress()
    }

    handleSelect = address => {
        this.setState({ address })
        geocodeByAddress(address)
            .then(results => getLatLng(results[0]))
            .then(latLng => {
                this.findTopClosestAddress(latLng)
            })
            .catch(error => console.error('Error', error))
    }

    findTopClosestAddress = latLng => {
        this.setState({ latLng })
        this.props.findTopClosestAddress(latLng.lat, latLng.lng)
        this.setState({ step: 1 })//address selected
    }

    onSelectDate = key => {
        this.setState({ step: 4, selectedIndex: key })//date selected
    }

    onSelectPerson = key => {
        this.setState({ step: 2, selectedPerson: this.props.suggestedPersonArray[key] })
    }

    handleSuggestTime = () => {

        const initBooking = {
            pick_time: this.state.selectedPerson.suggestDate,
            address_name: this.state.address,
            latitude: this.state.latLng.lat,
            longitude: this.state.latLng.lng,
            group_key: this.state.selectedPerson.sp_pk,
            s_email: this.state.selectedPerson.email,
        }

        this.props.setInitialBooking(initBooking)
        history.push('/app/dashboard/client-contact')
    }

    onSelectTime = key => {

        const selectedDate = this.state.selectedPerson.oneweekDateTime[this.state.selectedIndex]

        const bookedTime = selectedDate.date
        const year = bookedTime.getFullYear()
        const month = bookedTime.getMonth()
        const date = bookedTime.getDate()

        const time = selectedDate.timelist[key].time
        const timeaar = time.split(':')
        const hour = Number(timeaar[0]) - 11
        const minute = timeaar[1]

        const timeslot = new Date(Date.UTC(year, month, date, hour, minute, 0, 0))

        const initBooking = {
            pick_time: timeslot,
            address_name: this.state.address,
            latitude: this.state.latLng.lat,
            longitude: this.state.latLng.lng,
            group_key: this.state.selectedPerson.sp_pk,
            s_email: this.state.selectedPerson.email,
        }

        this.props.setInitialBooking(initBooking)
        history.push('/app/dashboard/client-contact')

    }

    generateUUID = () => { // Public Domain/MIT
        let d = new Date().getTime()
        if (typeof performance !== 'undefined' && typeof performance.now === 'function') {
            d += performance.now() //use high-precision timer if available
        }
        return 'xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx'.replace(/[xy]/g, function (c) {
            const r = (d + Math.random() * 16) % 16 | 0
            d = Math.floor(d / 16)
            return (c === 'x' ? r : ((r & 0x3) | 0x8)).toString(16)
        })
    }

    setStatus = () => {
        const now = moment()
        const events = this.state.events
        for (let e = 0; e < events.length; e++) {
            const eventItem = events[e]
            if (
                moment(now).isBetween(
                    moment(eventItem.start.dateTime),
                    moment(eventItem.end.dateTime)
                )
            ) {
                this.setState({
                    isBusy: true,
                })
                return false
            } else {
                this.setState({
                    isBusy: false,
                })
            }
        }
    }

    /////////////////
    render() {

        let searchOptions = null

        searchOptions = {
            location: new window.google.maps.LatLng(-34, 148),
            radius: 900,
            types: ['address'],
        }

        return (
            <div className="container-fluid">

                <div className="form-group">
                    {searchOptions
                        ? <PlacesAutocomplete
                            value={ this.state.address }
                            onChange={ this.handleChange }
                            onSelect={ this.handleSelect }
                            searchOptions={ searchOptions }
                        >
                            {({ getInputProps, suggestions, getSuggestionItemProps, loading }) => (
                                <div className="m-auto text-center">
                                    <input
                                        { ...getInputProps({
                                            placeholder: 'Search Places ...',
                                            className: 'large-text',
                                        }) }
                                    />
                                    <div className="autocomplete-dropdown-container">
                                        {loading && <div>Loading...</div>}
                                        {suggestions.map((suggestion, index) => {
                                            const className = suggestion.active
                                                ? 'suggestion-item--active'
                                                : 'suggestion-item'
                                            // inline style for demonstration purpose
                                            const style = suggestion.active
                                                ? { backgroundColor: '#fafafa', cursor: 'pointer' }
                                                : { backgroundColor: '#ffffff', cursor: 'pointer' }
                                            return (
                                                // eslint-disable-next-line react/no-array-index-key
                                                <div key={ index }
                                                    { ...getSuggestionItemProps(suggestion, {
                                                        className,
                                                        style,
                                                    }) }
                                                >
                                                    <span>{suggestion.description}</span>
                                                </div>
                                            )
                                        })}
                                    </div>
                                </div>
                            )}
                        </PlacesAutocomplete>
                        : ''}
                </div>

                {
                    this.state.step >= 1 && this.props.suggestedPersonArray
                        ? <div>
                            <Personlist suggestedPersons={ this.props.suggestedPersonArray }
                                selectedPerson={ this.state.selectedPerson }
                                onSelectPerson={ this.onSelectPerson }
                            />
                            <div className="m-auto text-center">
                                <button className="mr-10 mb-10 btn btn-primary" onClick={ e => this.handleSuggestTime() }>Select Suggest Time</button>
                                <button className="mr-10 mb-10 btn btn-primary" onClick={ e => this.setState({ step: 3 }) }>Pick another time?</button>
                            </div>

                        </div>
                        : ''
                }
                {
                    this.state.step >= 3 && this.state.selectedPerson
                        ? <Weeklydate
                            oneweekDateTime={ this.state.selectedPerson.oneweekDateTime }
                            onSelectDate={ this.onSelectDate }
                            show={ this.state.step >= 2 ? true : false }
                        />
                        : ''
                }
                {
                    this.state.step >= 4
                        ? <Timelist selectedDate={ this.state.selectedPerson.oneweekDateTime[this.state.selectedIndex] } onSelectTime={ this.onSelectTime } />
                        : ''
                }
                {this.props.loading &&
                    <RctSectionLoader />
                }
                <a
                    className="primary-cta"
                    href="https://calendar.google.com/calendar?cid=i1abpli3aeg294vnnjl2g9q8js@group.calendar.google.com"
                    target="_blank"
                >
                    G
                </a>
            </div>
        )
    }
}

export default DataPage
